@import "~/src/scss/index.scss";

.post-form{
    .ui.button{
        margin-top: 20px;
    }

    &__miniature{
        margin: 16px 0;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;

        > div{
            width: 200px;
            min-height: 120px;
            display: flex;
            align-items: center;
            justify-content: center;
            border: 2px dashed $primary;
        }
    
        &:hover{
            cursor: pointer;
            opacity: 0.6;
        }

        > .ui.image{
            width: 200px;
            min-height: 120px;
            margin: 0;
            border: 2px dashed $primary;
            padding: 5px;
            &:hover{
                opacity: 0.6;
                cursor: pointer;
            }
        }
    }    
}